import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Progress } from 'react-sweet-progress';
import 'react-sweet-progress/lib/style.css';
import axios from 'axios';
import styles from './FilmPage.module.css';
import { API_KEY } from '../../redux/filmsSearch/filmsSearchOperations';
import { ReactComponent as Heart } from '../../components/FilmsWrapper/heart.svg';
import { ReactComponent as Eye } from '../../components/FilmsWrapper/eye.svg';
import './FilmPage.css';

const FilmPage = ({
  films,
  match,
  query,
  addFilmToFavorites,
  markAsWatched,
  favoritesList,
  watchedList,
  deleteFromFavorites,
  deleteFromWatched,
  page,
}) => {
  const [chosenFilm, setChosenFilm] = useState(
    films.length > 0 && films.find(film => film.id === match.params.id),
  );

  useEffect(
    () => {
      setChosenFilm(
        films.length > 0 && films.find(film => film.id === match.params.id),
      );
    },
    // eslint-disable-next-line
    [match.params.id],
  );

  if (!chosenFilm) {
    axios
      .get(
        `https://api.themoviedb.org/3/search/movie?api_key=${API_KEY}&query=${match.params.title}&include_adult=false`,
      )
      .then(response => {
        const filmsResult = response.data.results;
        const requestFilm = filmsResult.find(
          film => film.id === Number(match.params.id),
        );
        if (requestFilm) {
          setChosenFilm(requestFilm);
        } else {
          const filmFromList =
            favoritesList.find(
              film => Number(film.id) === Number(match.params.id),
            ) ||
            watchedList.find(
              film => Number(film.id) === Number(match.params.id),
            );
          if (filmFromList) {
            setChosenFilm(filmFromList);
          } else {
            axios
              .get(
                `https://api.themoviedb.org/3/search/movie?api_key=${API_KEY}&query=${query}&page=${page}&include_adult=false`,
              )
              .then(resp => {
                const filmsFromPage = resp.data.results;
                const respFilm = filmsFromPage.find(
                  film => film.id === Number(match.params.id),
                );
                if (respFilm) {
                  setChosenFilm(respFilm);
                }
              });
          }
        }
      })
      // eslint-disable-next-line
      .catch(error => console.log(error));
  }
  const addToFavorites = () => {
    addFilmToFavorites(chosenFilm);
  };
  const addToWatched = () => {
    markAsWatched(chosenFilm);
  };
  const deleteChosenFilmFromFavorites = () => {
    deleteFromFavorites(chosenFilm.id);
  };
  const deleteChosenFilmFromWatched = () => {
    deleteFromWatched(chosenFilm.id);
  };
  return (
    <section className={styles.filmPage}>
      {' '}
      {chosenFilm && (
        <>
          <div className={styles.main}>
            <h2 className={styles.title}> {chosenFilm.title} </h2>{' '}
            <p className={styles.date}>
              <b> Release date: </b> {chosenFilm.release_date || 'No date'}{' '}
            </p>{' '}
            <p className={styles.language}>
              <b> Lanuage: </b>{' '}
              {chosenFilm.original_language || 'No language mentioned'}{' '}
            </p>{' '}
            <div className={styles.vote}>
              <p className={styles.vote}>
                <b> Vote average percentage: </b>{' '}
              </p>{' '}
              {chosenFilm.vote_average ? (
                <Progress
                  type="circle"
                  strokeWidth={13}
                  percent={chosenFilm.vote_average * 10}
                  status="default"
                  theme={{
                    default: {
                      color: '#cf7a05',
                      trailColor: '#918b8450',
                    },
                  }}
                  width={80}
                  className={styles.progress}
                />
              ) : (
                'No votes'
              )}{' '}
            </div>{' '}
            <p className={styles.overview}>
              <b className={styles.overviewTitle}> Overview </b> <br />{' '}
              {chosenFilm.overview || 'No overview available'}{' '}
            </p>{' '}
            <div className={styles.buttonWrapper}>
              {' '}
              {favoritesList.find(
                film => Number(film.id) === Number(chosenFilm.id),
              ) ? (
                <button
                  type="button"
                  onClick={deleteChosenFilmFromFavorites}
                  className={styles.button}
                  title="delete from favorites"
                >
                  <Heart className={styles.deleteFavButton} />{' '}
                </button>
              ) : (
                <button
                  type="button"
                  title="add to favorites"
                  onClick={addToFavorites}
                  className={styles.button}
                >
                  <Heart className={styles.favButton} />{' '}
                </button>
              )}{' '}
              {watchedList.find(
                film => Number(film.id) === Number(chosenFilm.id),
              ) ? (
                <button
                  type="button"
                  onClick={deleteChosenFilmFromWatched}
                  className={styles.button}
                  title="delete from watched"
                >
                  <Eye className={styles.deleteWatchedButton} />{' '}
                </button>
              ) : (
                <button
                  type="button"
                  onClick={addToWatched}
                  title="add to watched"
                  className={styles.button}
                >
                  <Eye className={styles.watchedButton} />{' '}
                </button>
              )}{' '}
            </div>{' '}
          </div>

          <img
            className={styles.filmImage}
            src={
              chosenFilm.poster_path
                ? `https://image.tmdb.org/t/p/w200/${chosenFilm.poster_path}`
                : 'https://www.viser.edu.rs/images/no_photo.jpeg'
            }
            alt="film poster"
          />
        </>
      )}{' '}
    </section>
  );
};

FilmPage.propTypes = {
  films: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  addFilmToFavorites: PropTypes.func.isRequired,
  markAsWatched: PropTypes.func.isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({
      title: PropTypes.string.isRequired,
      id: PropTypes.string.isRequired,
    }),
  }).isRequired,
  favoritesList: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  watchedList: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  deleteFromFavorites: PropTypes.func.isRequired,
  deleteFromWatched: PropTypes.func.isRequired,
  page: PropTypes.number.isRequired,
  query: PropTypes.string.isRequired,
};
export default FilmPage;
